*{
    box-sizing: border-box;
}

body, h1, h2, h3, p, ul, li {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}
ul, li {
      list-style-type: none;

}

.breed-select{
    width: 300px;
    margin: auto;
    
}

.loader,
.error{
    width: 250px;
margin: 30px;
text-align: center;
}